<template>
  <div>
    <dw-button type="primary" @click="addNew">添加</dw-button>
    <div class="list grid">
      <div class="item" v-for="(item, index) in list" :key="index">
        <div>第{{ index + 1 }}个</div>
        <div>name: <dw-input v-model="item.name" @input="fanyi(item, 'enNamePhr', $event)" /></div>
        <div>enName: <dw-input :placeholder="item.enNamePhr" v-model="item.enName" /></div>
        <div>desc: <dw-input v-model="item.desc" @input="fanyi(item, 'enDescPhr', $event)" /></div>
        <div>enDesc: <dw-input :placeholder="item.enDescPhr" v-model="item.enDesc" /></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
    };
  },
  methods: {
    addNew() {
      this.list.push({
        name: "",
        enName: "",
        enNamePhr: "",
        desc: "",
        enDesc: "",
        enDescPhr: "",
      });
    },
    async fanyi(row, key, val) {
      const res = await this.IM(val);
      row[key] = res;
    },
    IM(val) {
      return Promise.resolve(`en_${val}`);
    },
  },
};
</script>

<style lang="less" scoped>
.list {
  width: 100%;
  //   background: pink;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}
</style>
