<template>
  <div id="app">
    <nav-menu id="menu" v-model="curName" :list="componentsList"></nav-menu>
    <component :is="curName"></component>
  </div>
</template>

<script>
import navMenu from "./components/navMenu/index";
import myComponents from "./views/myComponents/index";
import todoList from "./views/todoList/index";
import jumpBall from "./views/jumpBall/index";
import demo from "./views/demo/index";

export default {
  name: "App",
  components: {
    navMenu,
    myComponents,
    todoList,
    jumpBall,
    demo,
  },
  data() {
    return {
      curName: "myComponents",
      componentsList: ["myComponents", "todoList", "jumpBall"],
    };
  },
};
// Vue.config.js 配置选项
</script>

<style lang="less">
/* @import "./styles/index.less"; */

#app {
  width: 100vw;
  height: 100vh;
  display: grid;
  justify-items: center;
  grid-template-rows: 15vh auto;
}
