<template>
  <div class="list-box">
    <transition-group :class="['list', { none: !list.length, show: isShowAll }]" tag="div" name="todo-list">
      <div class="item grid" v-for="(item, inx) in list" :key="item.id">
        <div :class="['item-text', { finish: item.status == 2 }]" :title="item.value">{{ item.value }}</div>
        <div>
          <dw-button type="warning" v-if="item.status == 0" waiting @click="updateRow(inx, 1)">执行中</dw-button>
          <dw-button type="primary" v-if="item.status == 1" @click="updateRow(inx, 0)">去执行</dw-button>
        </div>
        <div>
          <dw-button type="success" v-if="item.status != 2" @click="updateRow(inx, 2)">完成</dw-button>
        </div>
        <div>
          <dw-button type="danger" @click="deleteRow(inx)">删除</dw-button>
        </div>
      </div>
    </transition-group>
    <div>
      <dw-button type="info" class="pack-btn" :disabled="list.length <= 3" @click="packUp()">{{ isShowAll ? "收起" : "展开" }}</dw-button>
    </div>
  </div>
</template>

<script>
export default {
  props: ["list"],
  data() {
    return {
      isShowAll: true,
    };
  },

  methods: {
    updateRow(inx, status) {
      // this.isShowAll = false;
      this.$emit("update-row", inx, status);
    },
    deleteRow(inx) {
      this.$emit("delete-row", inx);
      this.isShowAll = true;
    },
    packUp() {
      if (this.list.length <= 3) return;
      this.isShowAll = !this.isShowAll;
    },
  },
};
</script>

<style scoped>
.list-box {
  margin-top: 50px;
  width: 100%;
}
.list {
  width: 100%;
  max-height: 150px;
  overflow: scroll;
  transition: max-height 0.5s;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}
/* 隐藏scroll滚动条但不影响滚动功能 */
.list::-webkit-scrollbar {
  display: none;
}
.none {
  border-top: 1px solid #d9d9d9;
  max-height: 50px;
}
.item {
  height: 50px;
  grid-template: 1fr/58% 16% 13% 13%;
  border-top: 1px solid #d9d9d9;
  padding: 0 10px;
  align-items: center;
}
.item-text {
  margin-right: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.finish {
  text-decoration: line-through;
}

.pack-btn {
  width: 100%;
  border-radius: 0 0 4px 4px;
}
.show {
  max-height: 62vh;
}
.todo-list-move {
  transition: transform 1s;
}
/* 离开动画 */
.todo-list-leave-active {
  animation: leave 0.5s ease-out;
}
@keyframes leave {
  to {
    opacity: 0;
    z-index: -1;
    transform: translateY(100%);
  }
}
@keyframes hide {
  to {
    transform-origin: tra;
  }
}
</style>
