<template>
  <div class="dw-input-box">
    <input class="dw-input" :class="{ disabled: disabled, clearable: clearable }" type="text" :value="value" :placeholder="placeholder" @input="$emit('input', $event.target.value)" />
    <span v-if="clearable" class="btn" @click="$emit('input', '')"></span>
  </div>
</template>

<script>
export default {
  name: "dwInput",
  model: {
    prop: "value",
    event: "input",
  },
  props: {
    value: {},
    placeholder: {
      type: String,
      default: "",
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

<style scoped>
@import "./assets/index.less";
.dw-input-box {
  position: relative;
  /* box-sizing: border-box; */
}
.disabled {
  background-color: #f5f7fa;
  border-color: #e4e7ed;
  color: #c0c4cc;
  cursor: not-allowed;
}
.clearable {
  padding-right: 30px;
}
.dw-input-box:hover .btn {
  width: 0.9em;
  height: 0.9em;
  background: url("~./assets/close.png");
  background-size: 100%;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-0.45em);
  cursor: pointer;
}
.dw-input-box .btn:hover {
  background: url("~./assets/close-hover.png");
  background-size: 100%;
}
</style>
