<template>
  <div class="box">
    <div class="title">TODO LIST</div>
    <input-btn @add="addNew"></input-btn>
    <todo-list :list="list" @update-row="updateRow" @delete-row="deleteRow"></todo-list>
  </div>
</template>

<script>
import inputBtn from "./components/inputBtn";
import todoList from "./components/todoList";

export default {
  name: "App",
  data() {
    return {
      list: [],
    };
  },
  components: {
    inputBtn,
    todoList,
  },
  watch: {},
  methods: {
    addNew(value, time) {
      const data = {
        id: time,
        value,
        status: 1, // 0在执行 1待执行 2已完成
        created: time,
        finished: null,
      };
      // 判断是否存在正在执行
      const inx = this.list.length && !this.list[0].status;
      this.list.splice(inx, 0, data);
    },
    // 更新
    updateRow(inx, status) {
      switch (status) {
        case 0:
          this.toDoing(inx);
          break;
        case 1:
          this.list[inx].status = 1;
          break;
        case 2:
          this.toFinish(inx);
          break;
      }
    },
    // 去执行
    toDoing(inx) {
      // 先修改位置
      const cur = this.list.splice(inx, 1)[0];
      this.list.unshift(cur);
      // transition-group bug？ 等位置移动的动画结束后 再修改值
      this.$nextTick(() => {
        this.list[0].status = 0;
        // 如果已经存在正在执行的
        if (this.list.length >= 2 && !this.list[1].status) this.list[1].status = 1;
      });
    },
    // 完成
    toFinish(inx) {
      const isFinishedNum = this.list.filter(({ status }) => status == 2).length;
      this.list[inx].status = 2;

      // 修改位置 判断已完成个数 放在最前面
      const cur = this.list.splice(inx, 1)[0];
      if (!isFinishedNum) {
        this.list.push(cur);
      } else {
        this.list.splice(-isFinishedNum, 0, cur);
      }
    },
    // 删除
    deleteRow(inx) {
      this.list.splice(inx, 1);
    },
  },
};
</script>

<style scoped>
.box {
  width: 600px;
  color: #666;
}
.title {
  font-size: 40px;
  font-weight: 600;
  color: #666;
  text-align: center;
  height: 10vh;
}
</style>
